import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store/index'

import Layout from '@/views/Layout'
import Home from '@/views/Layout/Home'

const Cart = () => import('@/views/Layout/Cart')
const Category = () => import('@/views/Layout/Category')
const User = () => import('@/views/Layout/User')

const Login = () => import('@/views/Login')
const CheckoutIndex = () => import('@/views/Checkout')

const Detail = () => import('@/views/GoodsDetail')
const CommentList = () => import('@/views/Comment')

const Search = () => import('@/views/Search/index')
const SearchResult = () => import('@/views/Search/SearchResult')

const Address = () => import('@/views/Address/index')
const CreateAdd = () => import('@/views/Address/CreateAdd')
const EditAddress = () => import('@/views/Address/EditAdd')

const Myorder = () => import('@/views/MyOrder')
const AddComment = () => import('@/views/MyOrder/AddComment')
const OrderDetail = () => import('@/views/MyOrder/OrderDetail')

const WalletIndex = () => import('@/views/Wallet')
const BalanceLog = () => import('@/views/Wallet/BalanceLog')
const RefundList = () => import('@/views/Refund')
const ApplyRefund = () => import('@/views/Refund/ApplyRefund')
const GetCoupon = () => import('@/views/Coupon/GetCoupon')
const MyCoupon = () => import('@/views/Coupon/MyCoupon')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: Layout,
      redirect: '/home',
      children: [
        { path: '/home', name: 'home', component: Home, meta: { keepAlive: true } },
        { path: '/category', name: 'category', component: Category, meta: { title: '', keepAlive: true } },
        { path: '/cart', name: 'cart', component: Cart, meta: { title: '' } },
        { path: '/user', name: 'user', component: User, meta: { title: '', keepAlive: true } }
      ]
    },
    { path: '/login', name: 'login', component: Login, meta: { title: '' } },
    { path: '/myorder', name: 'myorder', component: Myorder, meta: { title: '' } },
    { path: '/orderdetail', name: 'orderdetail', component: OrderDetail, meta: { title: '' } },
    { path: '/checkout', name: 'checkout', component: CheckoutIndex, meta: { title: '' } },
    { path: '/detail/:goodsId', name: 'detail', component: Detail, meta: { keepAlive: true } },
    { path: '/comment', name: 'comment', component: CommentList, meta: { title: '' } },
    { path: '/search', name: 'search', component: Search, meta: { title: '' } },

    { path: '/address', name: 'address', component: Address, meta: { title: '' } },
    { path: '/createadd', name: 'createadd', component: CreateAdd, meta: { title: '' } },
    { path: '/editadd', name: 'editadd', component: EditAddress, meta: { title: '' } },
    { path: '/addcomment', name: 'addcomment', component: AddComment, meta: { title: '' } },
    { path: '/refund', name: 'refund', component: RefundList, meta: { title: '' } },
    { path: '/applyrefund', name: 'applyrefund', component: ApplyRefund, meta: { title: '' } },
    { path: '/wallet', name: 'wallet', component: WalletIndex, meta: { title: '' } },
    { path: '/wallet/log', name: 'walletLog', component: BalanceLog, meta: { title: '' } },
    { path: '/coupon/get', name: 'getCoupon', component: GetCoupon, meta: { title: '' } },
    { path: '/mycoupon', name: 'mycoupon', component: MyCoupon, meta: { title: '' } },
    { path: '/searchresult', name: 'searchresult', component: SearchResult, meta: { keepAlive: true } }

  ],

  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      // console.log(savedPosition)
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

// 
const authUrls = ['/myorder', '/address', '/wallet',
  '/mycoupon', '/coupon/get', '/refund', '/checkout']
router.beforeEach((to, from, next) => {
  // 
  document.title = to.meta.title || ''
  const token = store.getters.ifToken
  // 
  if (!authUrls.includes(to.path)) {
    next()
  } else if (token) {
    next()
  } else {
    router.push({
      path: '/login',
      query: {
        backUrl: to.fullPath
      }
    })
  }
})

export default router
